<template>
  <div>
    <header class="header">
        <a class="mui-action-back mui-icon mui-icon-left-nav mui-pull-left"></a>
        <a href="javascript:;">
          <span class="mui-icon mui-icon-help"></span>
        </a>
    </header>
    <div class="mui-content">
        <img style="margin-top:0.45rem;margin-bottom:1.5rem;" src="@/components/icon/kb.png">
        <form class="mui-input-group">
            <div class="mui-input-row">
                <label>帐号</label>
                <input type="text" v-model="username" 
                v-focus="true" class="mui-input-clear" placeholder="请输入帐号名" id="username">
            </div>
            <div class="mui-input-row">
                <label>密码</label>
                <input type="text" v-model="password" class="mui-input-password" placeholder="请输入密码" id="password">
            </div>
        </form>
        <div class="mui-content-padded" align="center">
            <button type="button" class="mui-btn" :disabled="disabled" :class="em==true?'':'loginactive'" @click="login">登陆</button>
        </div>
        <div class="telReg">
          <router-link to="/register">手机号注册</router-link>
          <router-link to="javascript:;">短信认证登录</router-link>
        </div>
    </div>
  </div>
</template>
<script>
export default {
  data(){
    return{
      em:true,
      disabled:true,
      username:'',
      password:''
    }
  },
  created(){
  },
  mounted(){
    
  },
  methods:{
    login(){
       if(this.username.length==0){
         mui.toast('用户名不能为空')
          return;
        }
        if(this.password.length==0){
          mui.toast("密码不能为空");
          return;
        }
        mui.ajax("http://127.0.0.1:4000/user/login",{
          data:{
            uname:this.username,
            upwd:this.password
          },
          dataType:"json",
          type:"POST",
          success:(result)=>{
            if(result.code==200){
              mui.toast(result.msg)
              sessionStorage.setItem("uname",this.username);
              setTimeout(function(){
                mui.back();
              },500)
            }else if(result.code==302){
              mui.toast(result.msg)
            }
          },
          error:()=>{
            mui.toast("登录出错,请重新登录")
          }
        })
    },
    pd(){
      if(this.username.trim() !=="" && this.password.trim() !==""){
        this.em=false;
        this.disabled = false;
      }else{
        this.em=true;
        this.disabled = true;
      }
    }
  },
  watch:{
    username(){
      this.pd();
    },
    password(){
      this.pd();
    }
  }
}
</script>
<style scoped>
.header{
  padding: 0.375rem 0.75rem;
  background:#fff;
  display: flex;
  justify-content: space-between;
}
.header .mui-icon-help{
  color:#666;
  font-size:1.4rem;
  font-weight: bold;
}
a.mui-action-back{
  color: #222;
}
.mui-content{
  height:100%;
  background:#fff;
}

.mui-input-group:before{
  height:0;
  }

.mui-input-row label{
  width:auto;
  text-align: left;
  font-size: 0.9rem;
  font-weight:700;
}
.mui-input-row input{
  font-size: 0.9rem;
}
.mui-input-row label ~ input{
  float:left;
}
.mui-btn{
     width: 100%;
     height:2.3rem;
     background:#ccc;
     color:#fff;
     border-radius:2rem;
}
.mui-content-padded{
  margin-top:1.125rem;
}
.loginactive{
  background:linear-gradient(left,#ff5a00 33.33%,#ff7a00 33.33%,#ff5a00 33.33%) !important;
}
.telReg{
  display:flex;
  justify-content: space-between;
   margin-top:1rem;
  padding: 0.375rem 0.75rem;
}
.telReg a{
  color:#999;
  font-size: 0.8rem;
  }
</style>
