<script>
import Footer from '@/components/footer.vue'
import Header from '@/components/header.vue'
import Corouse from '@/components/muiCorouse.vue'
import tabNav from '@/components/table-view.vue'
import { setTimeout, setInterval, clearInterval } from 'timers';
export default {
  data(){
    return{
			show:false,
			pno:1,
			ref:[],
			head:{
				"opacity":0
			},
			opacity:{
				"opacity":1,
			},
			hours:20,
			minutes:32,
			seconds:45
		}
	},
	created(){
		this.$store.state.num=1;
		this.loadMore();
	},
	mounted(){
		setInterval(()=>{
			var day = new Date("2019/5/26");
			var now = new Date();
			var ms = day.getTime()-now.getTime();
			var m = Math.floor(ms/1000);
			var days = Math.floor(m/(24*60*60))
			var hours = m%(24*60*60);//剩余的天数 
					hours=Math.floor(hours/3600);//剩余小时
			var minutes = Math.floor((m%3600)/60);
			var seconds = m%60;
			(hours<10)&&(hours = "0"+hours);
			(minutes<10)&&(minutes = "0"+minutes);
			(seconds<10)&&(seconds = "0"+seconds);
			this.hours = hours;
			this.minutes = minutes;
			this.seconds = seconds;

		},1000)
	//下拉刷新
	  mui.init({ 
			pullRefresh : {
					container:"#refreshContainer",//下拉刷新容器标识，querySelector能定位的css选择器均可，比如：id、.class等
					down : {
						style:'circle',//必选，下拉刷新样式，目前支持原生5+ ‘circle’ 样式
						color:'#2BD009', //可选，默认“#2BD009” 下拉刷新控件颜色
						// height:'50px',//可选,默认50px.下拉刷新控件的高度,
					//	range:'100px', //可选 默认100px,控件可下拉拖拽的范围
					 	contentdown : "下拉可以刷新",
						contentover : "释放立即刷新",
						contentrefresh : "正在刷新...",
						offset:'0px', //可选 默认0px,下拉刷新控件的起始位置
						auto: false,//可选,默认false.首次加载自动上拉刷新一次
						indicators: false,
						callback :this.pulldownRefresh //必选，刷新函数，根据具体业务来编写，比如通过ajax从服务器获取新数据；
					},
				　　up:{
				　　　　contentrefresh: '正在加载...',
				　　　　contentnomore:'没有更多数据了',
				　　　　callback:this.pulluploading //上拉加载下一页
				　　}
				}
			});
			var scroll = mui('.mui-scroll-wrapper').scroll();
			document.querySelector('.mui-scroll-wrapper').addEventListener('scroll',(e) =>{
				// console.log(scroll.y);
				var y=scroll.y
			// 　　if(y>-10){
			// 			this.show =false; 
			// 			this.head['opacity']=0
			// 			this.opacity["opacity"] = 1;
			// 		}
					for(var o=1;o<3;o++){
							if(y>-3.33*o){
								this.show =false; 
								this.head['opacity']=0
								this.opacity["opacity"] = 0.3*o;
							}
							if(y>=0||this.opacity["opacity"] ==0.9){
								this.opacity["opacity"] =1;
							}
					}
					for(var i=1;i<6;i++){
						if(y<-10*i){
							this.show =true; 
							this.opacity["opacity"] =0;
							this.head['opacity']=0.2*i
						}
					}
			})
			
		
	},
	methods:{
		pulldownRefresh(){
			setTimeout(function() {
				mui('#refreshContainer').pullRefresh().endPulldownToRefresh();
				mui.toast("暂无更新");
				mui('#refreshContainer').pullRefresh().refresh(true);
			},1500)
		},
		pulluploading(){
			setTimeout(()=>{
				mui('#refreshContainer').pullRefresh().endPullupToRefresh(true);
				this.loadMore();
				mui('#refreshContainer').pullRefresh().refresh(true);
			},1500)
		},
		loadMore(){
			var pno = this.pno++;
			console.log(pno);
				mui.ajax("http://localhost:4000/refresh",{
					data:{
						pno:pno
					},
					dataType:"json",
					type:"get",
					success:(result)=>{
						if(result.code==200){
							
							for(var i=0;i<result.data.length;i++){
								this.ref.push(result.data[i])
							}
						}else if(result.code==302){
							mui.toast("没有更多了哦")
						}
					},
					error:()=>{
						console.log("加载出错")
					}
				})
		},
		search(){
				 this.$router.push("/search")
		}
	},
  components:{
	 Footer,
	 Header,
	 Corouse,
	 tabNav,
	}
	
}
</script>
